import React from 'react'
import {Redirect, Route} from 'react-router-dom'
//import routes from './router.config';

const PrivateRoute = ({ component: Com, ...rest }) => {
    return (
        <Route
            { ...rest }
            render={ ({ location }) =>
                localStorage.getItem('userinfo')
                    ? <Com />
                    : (
                        <Redirect
                            to={ {
                                pathname: "/login",
                                state: location
                            } }
                        />
                    )
            }
        />
    );
}

export default PrivateRoute;