import Home from './pages/Home';
import Doc from './pages/Doc';
import Tuto from './pages/Tuto';
import Blog from './pages/Blog';
import Comm from './pages/Comm';
import Login from './pages/Login';
import React from 'react'
// import DocContent from './pages/DocContent';
const Core = () => {
    return (
        <h1>core</h1>
    )
}
const Guide = () => {
    return (
        <h1>guide</h1>
    )
}
const Api = () => {
    return (
        <h1>api</h1>
    )
}
const Hooks = () => {
    return (
        <h1>hooks</h1>
    )
}
const routes = [
    {
        path: '/',
        exact: true,
        component: Home
    },
    {
        path: '/doc',
        component: Doc,
        pri: true,
        routes: [
            {
                path:'/doc/core',
                component:Core,
                //zi: true,
            },
            {
                path:'/doc/guide',
                component:Guide,
                //zi: true,
            },
            {
                path:'/doc/api',
                component:Api,
                //zi: true,
            },
            {
                path:'/doc/hooks',
                component:Hooks,
               //zi: true,
            }
        ]
    },
    {
        path: '/tuto',
        component: Tuto,
        pri: true
    },
    {
        path: '/blog',
        component: Blog
    },
    {
        path: '/comm',
        component: Comm
    },
    {
        path: '/login',
        component: Login
    },
]
export default routes;