import React from 'react'
// eslint-disable-next-line
import {
    NavLink, Redirect, withRouter,
    Switch
} from 'react-router-dom'
import './doc.css';
//import routes from '../router.config';
import RouteWithSubRoutes from '../RouteWithSubRoutes';
// function RouteWithSubRoutes(route) {
//     return (
//         <Route
//             path={route.path}
//             render={props => (
//                 // pass the sub-routes down to keep nesting
//                 <route.component {...props} routes={route.routes} />
//             )}
//         />
//     );
// }
//if(route.pri){
//         return <PrivateRoute {...route}/>
//     }
const Doc = (props) => {
    console.log(props);
    return (
        <div className='doc'>
            <div className="content">
                <Switch>
                    {props.routes.map((route, i) => (
                        <RouteWithSubRoutes key={i}{...route}/>
                    ))}
                </Switch>
                <Redirect from='/doc' to='/doc/core'/>
                {/*Redirect 不要与 Route 作为同级兄弟一起使用，否则页面会保持在 Redirect 指定的路由，而不能跳到其它的路由：*/}
            </div>
            <ol>
                <li><NavLink to='/doc/core'>核心概念</NavLink></li>
                <li><NavLink to='/doc/guide'>高级指引</NavLink></li>
                <li><NavLink to='/doc/api'>API</NavLink></li>
                <li><NavLink to='/doc/hooks'>Hooks</NavLink></li>
            </ol>
        </div>
    )
}

export default withRouter( Doc );
